import { useState } from 'react';

import _ from 'lodash';

import * as yup from 'yup';

import { yupVOValidation } from 'src/yup/utils';

import { commonValidationSchema } from '#/crud_configs/users/validation';
import { DXTPasswordStatus, dxtPasswordStatus } from 'src/domain/shared/validation';
import { VODXTPassword } from 'src/domain/shared/value_objects';

export const useCustomValidationSchema = () => {
  const [passwordStatus, setPasswordStatus] = useState<DXTPasswordStatus | null>(null);

  const customValidationSchema = yup
    .object({
      password: yup
        .string()
        .test('password', 'Formato de contraseña no válido', (v) => {
          if (v != null && v != '') {
            let newPasswordStatus = dxtPasswordStatus(v);

            if (!_.isEqual(passwordStatus, newPasswordStatus))
              setPasswordStatus(newPasswordStatus);

            return yupVOValidation(VODXTPassword, v);
          }

          setPasswordStatus(null);

          return true;
        }),
      password_repeat: yup
        .string()
        .when('password', {
          is: (value:string) => {
            return value != null && value != '';
          },
          then: (schema) => schema.required('Repita la contraseña'),
        })
        .test(
          'passwords-match',
          'Las contraseñas no coinciden',
          function (value) {
            if (this.parent.password != null && this.parent.password != '') {
              return this.parent.password === value;
            }
    
            return true;
          }
        ),
    })
    .required();

  const yupValidationSchema = commonValidationSchema.concat(customValidationSchema);

  return { yupValidationSchema, passwordStatus };
};